.bank-logo {
    background-image: url(../images/bank-logo.png?20170720);
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: 0 -4256px;
    display: block;
    width: 125px;
    height: 28px;
    overflow: hidden;
    text-indent: -9999px;
    margin: 0 auto;
}

.bank-box {
    padding: .5em 1em;
    border: 1px solid #ddd;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    -moz-transition: border-color .2s;
    -o-transition: border-color .2s;
    -webkit-transition: border-color .2s;
    transition: border-color .2s;
}

.bank-box .corner {
    position: absolute;
    top: -2px;
    right: -1px;
    color: #09f;
    font-size: 20px;
    display: none;
}

.bank-box.checked,
.bank-box:hover {
    border: 1px solid #09f;
}

.bank-box.checked .corner {
    display: block;
}

.bank-alipay {
    background-image: url(../images/ali-pay.png);
    background-position: center;
    background-size: auto 100%;
}

.bank-wechat {
    background-image: url(../images/wx-pay.png);
    background-position: center;
    background-size: auto 100%;
}
.bank-paypal{
    background-image: url(../images/paypal-pay.png);
    background-position: center;
    background-size: auto 100%;
}

